/*
  Able Player core styles

  Default z-index map:

  * Modal dialog
  div.able-modal-dialog = 10000
  div.able-modal-overlay = 9500

  * Items that should always be on top (9000):
	#able-vts-alert = 9400
  .able-alert = 9400
  .able-window-toolbar .able-button-handler-preferences = 9300
  .able-popup = 9200
  .able-volume-head = 9175
  .able-volume-slider = 9150
  .able-tooltip = 9000

  * Pop-ups with critical content:  (7000 - 8000):
  .able-sign-window = 8000
  .able-transcript-area = 7000

  * Player controls: (5000 - 6000)
  .able-controller .able-seekbar = 6900
  .able-controller .buttonOff = 6800
  .able-controller button > img = 6700
  .able-controller button > span = 6700
  .able-controller button = 6600
  .able-big-play-button = 6500
  div.able-captions-wrapper = 6000
  .able-seekbar-head = 5500
  .able-seekbar-played = 5200
  .able-seekbar-loaded = 5100
  .able = 5000
*/
.able-wrapper {
  position: relative;
  margin: 0;
  padding: 0;
  width: 100%;
  height: auto;
  -webkit-box-sizing: content-box !important;
          box-sizing: content-box !important;
  max-width: none !important;
}
.able {
  position: relative;
  margin: 1em 0;
  width: 100%; /* will be changed dynamically as player is contructed */
  z-index: 5000;
}
.able-column-left {
    float: left;
}
.able-column-right {
    float: left;
}
.able .able-vidcap-container {
  background-color: black;
  /* height: 360px; */
  left: 0;
  margin: 0;
  position: relative;
  top: 0;
}
.able-player {
  font-family: inherit;
  background-color: #262626; /* background color of player (appears on top & bottom) */
}
.able-offscreen {
  position:absolute;
  left:-10000px;
  top:auto;
  width:1px;
  height:1px;
  overflow:hidden;
}
.able-media-container audio {
  display: none !important;
}
.able-video {
}
.able-video .able-now-playing {
  display: none; /* not currently used for video */
}
.able-controller {
  position: relative;
  border-top: 2px solid #4c4c4c;
  border-bottom: 2px solid #4c4c4c;
  background-color: #464646; /* background color of controller bar */
  min-height: 38px; /* height of button (20px) + 4px border + 4px padding */
  padding: 0;
}
.able-poster {
  position: absolute;
  top: 0;
  left: 0;
}
/*
  Controller Buttons & Controls
*/
.able-big-play-button {
    position: absolute;
    font-size: 8em;
    opacity: 0.5;
    filter:alpha(opacity=50); /* for IE */
    color: #fdfdfd;
    background-color: transparent;
    border: none;
    outline: none;
    left: 0;
    top: 0;
    padding: 0;
    z-index: 6500;
}
.able-big-play-button:hover {
    opacity: 100;
    filter:alpha(opacity=100); /* for IE */
}
.able-left-controls, .able-right-controls {
    overflow: visible;
}
.able-left-controls button, .able-right-controls button {
    vertical-align: middle;
}
.able-left-controls {
  float: left;
}
.able-right-controls {
  float: right;
}
.able-black-controls,
.able-black-controls button,
.able-black-controls label  {
  color: #000 !important;
}
.able-black-controls .able-seekbar {
  border: 2px solid #000;
}
.able-white-controls,
.able-white-controls button,
.able-black-controls label {
  color: #FFF !important;
}
.able-white-controls .able-seekbar {
  border: 2px solid #FFF;
}
.able-controller button {
  background: none;
  position: relative;
  display: inline-block;
  border-style: none;
  margin: 3px;
  padding: 0;
  font-size: 20px;
  min-width: 24px;
  outline: 2px solid #464646; /* same color as .able-controller background-color */
  border: none;
  overflow: visible !important;
  z-index: 6600;
}
.able-controller button > span,
.able-controller button > img {
  width: 20px;
  margin: 0 auto;
  padding: 0;
  z-index: 6700;
}
.able-controller .buttonOff {
  opacity: 0.5;
  filter:alpha(opacity=50); /* for IE */
  z-index: 6800;
}
.able-controller .able-seekbar {
  margin: 0 5px;
  z-index: 6900;
}
.able-controller button:hover,
.able-controller button:focus {
  outline-style: solid;
  outline-width: medium;
}
.able-controller button:hover {
  outline-color: #8AB839 !important; /* green */
}
.able-controller button:focus {
  outline-color: #ffbb37 !important; /* yellow */
}
/*
  Seekbar
*/
.able-seekbar-wrapper {
  display: inline-block;
  vertical-align: middle;
}
.able-seekbar {
  position: relative;
  height: 0.5em;
  border: 1px solid;
  background-color: #000000;
  margin: 0 3px;
  border-style: solid;
  border-width: 2px;
  border-color: #ffffff;
}
.able-seekbar-loaded {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  height: 0.5em;
  background-color: #464646;
  z-index: 5100;
}
.able-seekbar-played {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  height: 0.5em;
  background-color: #DADADA;
  z-index: 5200;
}
.able-seekbar-head {
  display: inline-block;
  position: relative;
  left: 0;
  top: -0.45em;
  background-color: #FDFDFD;
  width: 0.8em;
  height: 0.8em;
  border: 1px solid;
  border-radius: 0.8em;
  z-index: 5500;
}
/*
  Volume Slider
*/
.able-volume-slider {
  width: 34px;
  height: 50px;
  background-color: #464646; /* same as .able-controller */
  padding: 10px 0;
  position: absolute;
  right: 0px;
  top: -74px;
  display: block;
  z-index: 9100;
}
.able-volume-track {
  display: block;
  position: relative;
  height: 100%; /* = 50px after padding */
  width: 5px;
  margin: 0 auto;
  background-color: #999;
}
.able-volume-track.able-volume-track-on {
  background-color: #dadada;
  position: absolute;
  height: 20px; /* change dynamically; 5px per volume tick */
  top: 30px;  /* change dynamically; 50px - height */
}
.able-volume-head {
  display: inline-block;
  background-color: #fdfdfd;
  outline: 1px solid #333;
  position: absolute;
  height: 7px;
  width: 15px;
  left: -5px;
  top: 23px; /* change dynamically; .able-volume-track-on top - 7 */
  z-index: 9175;
}
.able-volume-head:hover,
.able-volume-head:focus {
  background-color: #ffbb37 !important; /* yellow */
}
.able-volume-help { /* not visible; used in aria-describedby */
  display: none;
}
