/*
  Status Bar
*/
.able-status-bar {
  height: 1.5em;
  min-height: 1.5em;
  color: #CCC;
  font-size: 0.9em;
  background-color: transparent;
  padding: 0.5em 0.5em 0.25em;
}
.able-status-bar span.able-timer {
  /* contains both span.elapsedTime and span.duration */
  text-align: left;
  float: left;
  width: 32%;
}
.able-status-bar span.able-speed {
  float: left;
  width: 33%;
  text-align: center;
}
.able-status {
  font-style: italic;
  float: right;
  width: 32%;
  text-align :right;
}
/*
  Captions and Descriptions
*/
div.able-captions-wrapper {
  width: 100%;
