/* Solarized Light

For use with Jekyll and Pygments

http://ethanschoonover.com/solarized

SOLARIZED HEX      ROLE
--------- -------- ------------------------------------------
base01    var(--w3c-blue)  body text / default code / primary content
base1     var(--dk-grey)  comments / secondary content
base3     #fdf6e3  background
orange    var(--faded-red)  constants
red       var(--faded-red)  regex, special keywords
blue      var(--w3c-classic)  reserved keywords
cyan      var(--wai-green)  strings, numbers
green     var(--wai-green)  operators, other keywords

*/
pre {
  white-space: pre-wrap;
}
.highlight {
  font-size: 14px;
  font-size: .875rem;
  color: #003366;
  color: var(--w3c-blue);
  -moz-tab-size: 2;
    -o-tab-size: 2;
       tab-size: 2;
}
.highlight .c   { color: #3b3b3b; color: var(--dk-grey) }
/* Comment */
.highlight .err { color: #003366; color: var(--w3c-blue) }
/* Error */
.highlight .g   { color: #003366; color: var(--w3c-blue) }
/* Generic */
.highlight .k   { color: #005A6A; color: var(--wai-green) }
/* Keyword */
.highlight .l   { color: #003366; color: var(--w3c-blue) }
/* Literal */
.highlight .n   { color: #003366; color: var(--w3c-blue) }
/* Name */
.highlight .o   { color: #005A6A; color: var(--wai-green) }
/* Operator */
.highlight .x   { color: #c0272d; color: var(--faded-red) }
/* Other */
.highlight .p   { color: #003366; color: var(--w3c-blue) }
/* Punctuation */
.highlight .cm  { color: #3b3b3b; color: var(--dk-grey) }
/* Comment.Multiline */
.highlight .cp  { color: #005A6A; color: var(--wai-green) }
/* Comment.Preproc */
.highlight .c1  { color: #3b3b3b; color: var(--dk-grey) }
/* Comment.Single */
.highlight .cs  { color: #005A6A; color: var(--wai-green) }
/* Comment.Special */
.highlight .gd  { color: #005A6A; color: var(--wai-green) }
/* Generic.Deleted */
.highlight .ge  { color: #003366; color: var(--w3c-blue); font-weight: bold }
/* Generic.Emph */
.highlight .gr  { color: #c0272d; color: var(--faded-red) }
/* Generic.Error */
.highlight .gh  { color: #c0272d; color: var(--faded-red) }
/* Generic.Heading */
.highlight .gi  { color: #005A6A; color: var(--wai-green) }
/* Generic.Inserted */
.highlight .go  { color: #003366; color: var(--w3c-blue) }
/* Generic.Output */
.highlight .gp  { color: #003366; color: var(--w3c-blue) }
/* Generic.Prompt */
.highlight .gs  { color: #003366; color: var(--w3c-blue); font-weight: bold }
/* Generic.Strong */
.highlight .gu  { color: #c0272d; color: var(--faded-red) }
/* Generic.Subheading */
.highlight .gt  { color: #003366; color: var(--w3c-blue) }
/* Generic.Traceback */
.highlight .kc  { color: #c0272d; color: var(--faded-red) }
/* Keyword.Constant */
.highlight .kd  { color: #005A9C; color: var(--w3c-classic) }
/* Keyword.Declaration */
.highlight .kn  { color: #005A6A; color: var(--wai-green) }
/* Keyword.Namespace */
.highlight .kp  { color: #005A6A; color: var(--wai-green) }
/* Keyword.Pseudo */
